














































































































































































.upload-widget {
}
.upload-drop-zone {
    background-color: #f8f8f8;
    outline: 1px dashed #ccc;
    padding: 20px;
    margin-bottom: 15px;
    outline-offset: -10px;
    transition: all 0.4s;

    &.drag-over {
        background-color: #eee;
        outline-color: #000;
        .upload-widget-icon {
            opacity: 1;
        }
    }

    form {
        text-align: center;
    }

    label {
        display: block;
        cursor: pointer;
        font-size: 1.5em;
        font-weight: bold;
        span {
            font-weight: normal;
        }
    }

    input[type="file"] {
        display: none;
    }
}

.upload-widget-icon {
    margin: 0 auto;
    opacity: 0.2;
    transition: opacity 0.4s;
}
